.App {
  margin-top: 5%;
  margin-bottom: 1vh;
  margin-left: 5%;
  margin-right: 5%;
}

h1{
  font-size: 5vw;
}

.Details{
  font-weight: 100;
  font-size: 15pt;
}

.Content{
  display: flex;
  justify-content: space-around;
}

a{
  margin-right: 20pt;
}

.Picture{
  margin-left: 50pt;
  height: 40vmin;
  width: 40vmin;
  object-fit: cover;
  border-radius: 50%;
}


@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .Content{
    display: flex;
    flex-wrap: wrap-reverse;
  }
  .Picture{
    margin-left: 0pt;
  }
  h1{
    text-align: center;
    font-size: 10vw;
  }
  .Links{
    text-align: center;
  }
}